.page-layout {
  display: flex;
  flex-direction: column;
  align-items: center;

  height: 100%;
  width: 100%;
}

.page-layout__content {
  flex: 1;
  flex-basis: auto;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;

  margin-top: 8rem;
  max-width: 120rem;
  width: 100%;
}

.page-footer {
  display: flex;
  justify-content: center;
  flex-shrink: 0;

  width: 100%;

  margin-top: 2.4rem;

  background-color: var(--dark-aluminium);

  color: var(--light-aluminium);
  font-weight: 500;
  text-align: center;

  /* responsive */

  font-size: 1.6rem;
  line-height: 2.4rem;
}

.page-footer-grid {
  display: grid;
  grid-template-rows: auto auto;

  width: 100%;
  max-width: 120rem;

  /* responsive */

  padding: 3.2rem;
}

.page-footer-grid__info {
  display: grid;

  /* responsive */

  grid-template-columns: 1.5fr 1fr auto;
  column-gap: 2.4rem;
}

.page-footer-grid__brand {
  display: grid;

  justify-items: center;

  grid-template-columns: 1fr;
  margin-top: 3.2rem;
}

.page-footer-info__message {
  /* responsive */

  text-align: left;
}

.page-footer-message__headline {
  margin: 0 0 1.4rem;

  font-weight: 500;
}

.page-footer-message__description {
  margin-bottom: 0;

  letter-spacing: 0.016rem;
}

.page-footer-info__button {
  display: flex;
  justify-content: center;
  align-items: center;
}

.page-footer-info__resource-list {
  display: grid;

  /* responsive */

  row-gap: 0.8rem;
}

.page-footer-info__resource-list-item {
  text-align: right;
}

.page-footer-brand {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
}

.page-footer-brand__logo {
  width: 20px;
  margin-right: 12px;
}

.page-footer__hyperlink {
  letter-spacing: 0.001rem;
}

.page-footer__hyperlink,
.page-footer__hyperlink:active,
.page-footer__hyperlink:visited {
  color: var(--white);
}

.page-footer__hyperlink:hover {
  color: var(--aluminium);
}

.loader {
  height: 5rem;
  width: 5rem;

  margin: auto;

  animation: spin 2s infinite linear;
}

.code-snippet {
  display: flex;
  flex-direction: column;
  width: 100%;
  border-radius: 0.8rem;
  margin-top: 3.2rem;
  overflow: hidden;
}

.code-snippet__title {
  height: 4.8rem;
  width: 100%;

  background-color: var(--aluminium);
  color: var(--black);

  font-family: "Fira Code", source-code-pro, Menlo, Monaco, Consolas,
    "Courier New", monospace;
  font-weight: 600;

  /*  responsive */

  font-size: 1.6rem;
  line-height: 2.4rem;
  padding: 1.2rem 1.6rem;
}

.code-snippet__container {
  min-height: 32.4rem;
  background-color: var(--dark-aluminium);
  overflow-x: auto;
}

.code-snippet__wrapper {
  display: inline-block;
  padding: 32px;
}

.code-snippet__body {
  margin: 0;

  color: var(--white);
  font-size: 16px;
  line-height: 32px;
  word-wrap: break-word;
  white-space: pre-wrap;
}

.content-layout {
  flex: 1;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;

  width: 100%;
  min-height: 640px;

  /* responsive */

  padding: 48px;
}

.content__title {
  margin-top: 0;

  color: var(--white);
}

.content__body {
  /* responsive */

  font-size: 1.6rem;
  line-height: 2.4rem;
}

#page-description {
  display: flex;
  flex-direction: column;
}

#page-description span {
  margin-bottom: 1.6rem;
}

#page-description span:last-child {
  margin-bottom: 0;
}

.profile-grid {
  display: flex;
  flex-direction: column;

  /* responsive */

  margin-top: 48px;
}

.profile__header {
  display: grid;
  grid-template-columns: auto 1fr;
  column-gap: 16px;
}

.profile__avatar {
  border: solid 2px var(--aqua);
  border-radius: 50%;

  /* responsive */

  height: 80px;
  width: 80px;
}

.profile__headline {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.profile__title {
  margin-top: 0;
  margin-bottom: 0;

  color: var(--white);
}

.profile__details {
  /* responsive */

  margin-top: 32px;
}